*,
*::before,
*::after {
  box-sizing: border-box; }

html,
body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Helvetica, Arial, sans-serif;
  font-size: 13px;
  color: #666;
  background: #fff; }

.container {
  height: 400px;
  width: 300px; }
  .container .panels {
    position: relative;
    height: 100%;
    width: 100%;
    overflow: hidden; }
    .container .panels.panel_basis .basis {
      margin-top: 0; }
    .container .panels.panel_advanced .basis {
      margin-top: -400px; }

.basis {
  display: flex;
  flex-direction: column;
  height: 100%;
  margin-top: 0;
  transition: all 0.3s ease; }
  .basis .header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 40px;
    padding: 0 10px; }
    .basis .header .title {
      color: #444;
      text-decoration: none; }
    .basis .header .feedback {
      color: #666;
      text-decoration: none; }
  .basis .main {
    flex: 1;
    display: flex;
    justify-content: center;
    align-items: center;
    border-top: 1px solid #e6e6e6; }
    .basis .main .btn {
      display: flex;
      justify-content: center;
      align-items: center;
      width: 120px;
      height: 120px;
      border-radius: 50%;
      cursor: pointer;
      transition: all 0.3s ease;
      color: #fff; }
      .basis .main .btn:hover {
        opacity: 0.8; }
      .basis .main .btn.startRecord {
        background: #ff1b1b;
        border: 15px solid #ffa5a5; }
      .basis .main .btn.stopRecord {
        background: #1b5fff;
        border: 15px solid #9bd1ff; }
      .basis .main .btn.download {
        background: #f45a8d;
        border: 15px solid #ff9bbc; }
  .basis .info {
    height: 120px;
    padding: 10px;
    border-top: 1px solid #e6e6e6;
    background: #f7f7f7; }
    .basis .info .record_config .item {
      margin-bottom: 15px; }
      .basis .info .record_config .item .name {
        font-size: 12px;
        margin-bottom: 5px;
        color: #666; }
      .basis .info .record_config .item .value {
        display: flex;
        align-items: center; }
        .basis .info .record_config .item .value input {
          width: 100%;
          outline: none;
          padding: 0 5px;
          font-size: 12px;
          height: 25px;
          line-height: 25px;
          color: #666; }
  .basis .footer {
    height: 30px;
    line-height: 30px;
    text-align: center;
    cursor: pointer;
    letter-spacing: 2px;
    transition: all 0.3s ease;
    color: rgba(0, 0, 0, 0.5);
    border-top: 1px solid #e6e6e6; }
    .basis .footer:hover {
      color: rgba(0, 0, 0, 0.8); }

.advanced {
  height: 100%; }
  .advanced .header {
    height: 30px;
    line-height: 30px;
    text-align: center;
    cursor: pointer;
    letter-spacing: 2px;
    transition: all 0.3s ease;
    color: rgba(0, 0, 0, 0.5);
    border-bottom: 1px solid #e6e6e6; }
    .advanced .header:hover {
      color: rgba(0, 0, 0, 0.8); }
